export {
	textFromGroupAstRoot,
	textFromRelalgAstNode,
	textFromRelalgAstRoot,
} from './translate/textFromAst';

export {
	relalgFromRelalgAstNode,
	relalgFromRelalgAstRoot,
	relalgFromSQLAstRoot,
} from './translate/relalgFromAst';

export {
	replaceVariables
} from './translate/replaceVariables';


const peg_parser_ra = require('./parser/parser_ra.min.js');
const peg_parser_sql = require('./parser/parser_sql.min.js');

export function parseSQLSelect(text){
	return peg_parser_sql.parse(text, {startRule: 'start'});
}
export function parseSQLDump(text){
	return peg_parser_sql.parse(text, {startRule: 'dbDumpStart'});
}
export function parseRelalg(text, relationNames){
	return peg_parser_ra.parse(text, {startRule: 'start', relationNames: relationNames});
}
export function parseRelalgGroup(text){
	return peg_parser_ra.parse(text, {startRule: 'groupStart'});
}

import {replaceVariables} from './translate/replaceVariables'
import {relalgFromRelalgAstRoot} from './translate/relalgFromAst'
export function executeRelalg(text, relations){
	relations = relations || {};
	var ast = parseRelalg(text, Object.keys(relations));
	replaceVariables(ast, relations);
	var root = relalgFromRelalgAstRoot(ast, relations);
	root.check();
	return root;
}
