import { ExecutionError } from './ExecutionError'
import { RANode } from './RANode'
import { Schema } from './Schema'
import { Column } from './Column'

/**
 * relational algebra anti-join operator
 *
 * the columns that should be renamed are added via `addRenaming()`
 *
 * @extends RANode
 * @constructor
 * @param   {RANode}        child the left child expression
 * @returns {RenameColumns}
 */
export class RenameColumns extends RANode {
	constructor(child) {
		super('&rho;');

		this._renameList = [];

		this.setChild(child);
		this._schema = null;
	}

	getSchema() {
		return this._schema;
	}

	addRenaming(newName, oldName, oldRelAlias) {
		this._renameList.push({
			newName: newName,
			oldName: oldName,
			oldRelAlias: oldRelAlias
		});
	}

	check() {
		this._child.check();

		var schemaA = this._child.getSchema().copy();
		var schema = new Schema();
		var list = this._renameList;
		var i, j, oldColumn, columnRenamed, e;

		// check the rename list
		for (i = 0; i < list.length; i++) {
			if (schemaA.getColumnIndex(list[i].oldName, list[i].oldRelAlias, false) === -1) {
				this.throwExecutionError(i18n.t('db.messages.exec.error-column-not-found-name', {column: Column.printColumn(list[i].oldName, list[i].oldRelAlias), schema: schemaA.toString()}));
			}
		}


		// create the new schema
		for (i = 0; i < schemaA.getSize(); i++) {
			oldColumn = schemaA.getColumn(i);

			columnRenamed = false;
			for (j = 0; j < list.length; j++) {
				e = list[j];
				if (e.oldName == oldColumn.getName() && (e.oldRelAlias === null || e.oldRelAlias == oldColumn.getRelAlias())) {

					// add column with new name
					schema.addColumn(
						e.newName,
						oldColumn.getRelAlias(),
						oldColumn.getType()
					);

					columnRenamed = true;
					break;
				}
			}

			if (columnRenamed)
				continue;

			// add the not renamed column with its original
			schema.addColumn2(schemaA.getColumn(i));
		}

		this._schema = schema;
	}

	getResult(session) {
		session = this._returnOrCreateSession(session);

		var res = this._child.getResult(session).copy();
		res.setSchema(this.getSchema());

		this.setResultNumRows(res.getNumRows());
		return res;
	}

	getArgumentHtml() {
		var out = [];
		var list = this._renameList;
		for (var i = 0; i < list.length; i++) {
			var e = list[i];
			out.push(e.newName + '←' + Column.printColumn(e.oldName, e.oldRelAlias));
		}

		return out.join(', ');
	}
}