export class ExecutionError extends Error {
	constructor(message, codeInfo) {
		super(message);
		/* codeInfo
		 {
		 line: -1,
		 column: -1,
		 offset: -1,
		 text: ''
		 };
		 */
		if (codeInfo) {
			this.message = message;
			this.line = codeInfo.line;
			this.column = codeInfo.column;
			this.codeInfo = codeInfo;
		}
		else {
			this.message = message;
		}
	}
}