import * as ValueExpr from '../exec/ValueExpr'
import { Schema } from '../exec/Schema'
import { Column } from '../exec/Column'
import { Table } from '../exec/Table'
import { RANode } from '../exec/RANode'
import { Projection } from '../exec/Projection'
import { Union } from '../exec/Union'
import { Intersect } from '../exec/Intersect'
import { Division } from '../exec/Division'
import { Difference } from '../exec/Difference'
import { Selection } from '../exec/Selection'
import { GroupBy } from '../exec/GroupBy'
import { OrderBy } from '../exec/OrderBy'
import { InnerJoin } from '../exec/InnerJoin'
import { LeftOuterJoin } from '../exec/LeftOuterJoin'
import { RightOuterJoin } from '../exec/RightOuterJoin'
import { FullOuterJoin } from '../exec/FullOuterJoin'
import { CrossJoin } from '../exec/CrossJoin'
import { SemiJoin } from '../exec/SemiJoin'
import { AntiJoin } from '../exec/AntiJoin'
import { RenameColumns } from '../exec/RenameColumns'
import { RenameRelation } from '../exec/RenameRelation'
import { Relation } from '../exec/Relation'


// translate a SQL-AST to RA
export function relalgFromSQLAstRoot(astRoot, relations){
	'use strict';

	function setCodeInfoFromNode(raNode, astNode){
		if(!astNode.codeInfo){
			throw new Error('should not happen');
		}

		raNode.setCodeInfo(
			astNode.codeInfo.line,
			astNode.codeInfo.column,
			astNode.codeInfo.offset,
			astNode.codeInfo.text
		);
	}

	function getError(msg, codeInfo){
		var error = new Error(msg);
		error.line = codeInfo.line;
		error.column = codeInfo.column;
		error.codeInfo = codeInfo;
		return error;
	}

	function rec(n){
		var node, rel;
		var a, b, tmp, i;
		var condition;
		if(typeof(n.cond) !== 'undefined')
			condition = n.cond === null || n.cond instanceof Array ? n.cond : rec(n.cond);

		switch(n.type){
			case 'relation':
				if(typeof(relations[n.name]) == 'undefined'){
					throw getError(i18n.t('db.messages.translate.error-relation-not-found', {name: n.name}), n.codeInfo);
				}
				rel = relations[n.name].copy();
				if(n.relAlias === null){
					node = rel;
					break;
				}
				node = new RenameRelation(rel, n.relAlias);
				break;

			case 'valueExpr':
				if(n.datatype == 'null' && n.func === 'columnValue'){
					node = new ValueExpr.ValueExprColumnValue(n.args[0], n.args[1]);
				}
				else{
					switch(n.datatype){
						case 'string':
						case 'number':
						case 'boolean':
						case 'date':
						case 'null': // all with unknown type
							tmp = [];
							for(i = 0; i < n.args.length; i++){
								if(n.func === 'constant')
									tmp.push(n.args[i]);
								else
									tmp.push( rec(n.args[i]) );
							}

							node = new ValueExpr.ValueExprGeneric(n.datatype, n.func, tmp);
							break;
						default:
							throw 'not implemented yet';
					}
				}
				break;

			case 'statement':
				node = parseStatement(n);

				if(n.select.distinct === false){
					node.addWarning(i18n.t('db.messages.translate.warning-distinct-missing'), n.codeInfo);
				}
				break;

			case 'renameRelation':
				node = new RenameRelation(rec(n.child), n.newRelAlias);
				break;

			case 'relationFromSubstatement':
				rel = rec(n.statement);
				node = new RenameRelation(rel, n.relAlias);
				break;

			case 'innerJoin':
				node = new InnerJoin(rec(n.child), rec(n.child2), condition);
				break;
			case 'leftOuterJoin':
				node = new LeftOuterJoin(rec(n.child), rec(n.child2), condition);
				break;
			case 'rightOuterJoin':
				node = new RightOuterJoin(rec(n.child), rec(n.child2), condition);
				break;
			case 'fullOuterJoin':
				node = new FullOuterJoin(rec(n.child), rec(n.child2), condition);
				break;
			case 'crossJoin':
				node = new CrossJoin(rec(n.child), rec(n.child2));
				break;
			case 'naturalJoin':
				node = new InnerJoin(rec(n.child), rec(n.child2), null);
				break;

			case 'union':
				node = new Union(rec(n.child), rec(n.child2));
				break;
			case 'intersect':
				node = new Intersect(rec(n.child), rec(n.child2));
				break;
			case 'except':
				node = new Difference(rec(n.child), rec(n.child2));
				break;

			case 'orderBy':
				node = (function(n){
					var orderCols = [];
					var orderAsc = [];
					for(i = 0; i < n.arg.value.length; i++){
						var e = n.arg.value[i];

						orderAsc.push(e.asc);
						orderCols.push(new Column(e.col.name, e.col.relAlias));
					}
					node = new OrderBy(rec(n.child), orderCols, orderAsc);
					return node;
				})(n);
				break;

			case 'limit':
				node = (function(n){
					var limit = n.limit;
					var offset = n.offset;

					var conditionOffset = new ValueExpr.ValueExprGeneric('boolean', '>', [
						new ValueExpr.ValueExprGeneric('number', 'rownum', []),
						new ValueExpr.ValueExprGeneric('number', 'constant', [offset])
					]);

					if(limit === -1){
						// === LIMIT ALL => only offset
						node = new Selection(rec(n.child), conditionOffset);
					}
					else{
						// limit and offset
						var conditionLimit = new ValueExpr.ValueExprGeneric('boolean', '<=', [
							new ValueExpr.ValueExprGeneric('number', 'rownum', []),
							new ValueExpr.ValueExprGeneric('number', 'constant', [limit+offset])
						]);
						node = new Selection(rec(n.child), new ValueExpr.ValueExprGeneric('boolean', 'and', [conditionOffset, conditionLimit]));
					}
					return node;
				})(n);
				break;

			default:
				var e = new Error('type '+n.type+' not implemented');
				e.line = n.codeInfo.line;
				e.column = n.codeInfo.column;
				e.codeInfo = n.codeInfo;
				throw e;
		}

		if((n.type === 'union' || n.type === 'intersect' || n.type === 'except') && n.all === true){
			node.addWarning(i18n.t('db.messages.translate.warning-ignored-all-on-set-operators'), n.codeInfo);
		}

		if(n.wrappedInBrackets === true){
			node.setWrappedInBrackets(true);
		}

		setCodeInfoFromNode(node, n);

		return node;
	}

	function getSelection(root, condition, codeInfo){
		root.check();
		return new Selection(root, rec(condition));
	}


	function parseStatement(statement){
		var root, i;
		var projectionArgs = statement.select.arg;
		var col;
		var projections;

		// from-CLAUSE
		root = rec(statement.from);
		setCodeInfoFromNode(root, statement.from);
		root.check();

		// selection
		if(statement.where !== null){
			root = getSelection(root, statement.where.arg, statement.where.codeInfo);
			setCodeInfoFromNode(root, statement.where);
		}

		// groupby + aggregation
		if(statement.groupBy !== null || statement.numAggregationColumns > 0){
			var aggregateFunctions = [];
			var groupByCols = statement.groupBy || [];

			// filter aggFunctions from SELECT list
			for(i = 0; i < projectionArgs.length; i++){
				col = projectionArgs[i];
				if(col.type === 'aggFunction')
					aggregateFunctions.push(col);
			}

			if(aggregateFunctions.length > 0)
				root = new GroupBy(root, groupByCols, aggregateFunctions);
			else{
				// use projection if no aggregation is used
				projections = [];
				for(i = 0; i < groupByCols.length; i++){
					col = groupByCols[i];
					projections.push(new Column(col.name, col.relAlias));
				}
				root = new Projection(root, projections);
			}
		}


		// having
		if(statement.having !== null){
			root = getSelection(root, statement.having.arg, statement.having.codeInfo);
			setCodeInfoFromNode(root, statement.having);
		}

		// projection
		var colsRenamed = false;
		if(projectionArgs.length == 1 && projectionArgs[0].type === 'column' && projectionArgs[0].name === '*' && projectionArgs[0].relAlias === null){
			// select * => no projection needed
		}
		else{
			projections = [];
			for(i = 0; i < projectionArgs.length; i++){
				col = projectionArgs[i];

				if(col.type === 'aggFunction'){
					projections.push(new Column(col.name, null)); // has been renamed by gamma
				}
				else if(col.type === 'namedColumnExpr'){
					projections.push({
						name: col.name,
						relAlias: col.relAlias,
						child: rec(col.child)
					});
				}
				else if(col.type === 'column'){
					// normal columns
					projections.push(new Column(col.name, col.relAlias));

					if(col.alias !== null)
						colsRenamed = true;
				}
				else{
					throw 'this should not happen';
				}
			}
			root = new Projection(root, projections);
			setCodeInfoFromNode(root, statement.select);
		}


		// rename columns
		if(colsRenamed === true){
			root = new RenameColumns(root);

			for(i = 0; i < projectionArgs.length; i++){
				if(projectionArgs[i].type === 'column' && projectionArgs[i].alias !== null){
					root.addRenaming(projectionArgs[i].alias, projectionArgs[i].name, projectionArgs[i].relAlias);
				}
			}
		}

		return root;
	}

	return rec(astRoot.child);
}


// translates a RA-AST to RA
export function relalgFromRelalgAstRoot(astRoot, relations){
	// root is the real root node! of a statement
	return relalgFromRelalgAstNode(astRoot.child, relations);
}

/**
 * translates a RA-AST node to RA
 * @param   {Object} astNode   a node of a RA-AST
 * @param   {Object} relations hash of the relations that could be used in the statement
 * @returns {Object} an actual RA-expression
 */
export function relalgFromRelalgAstNode(astNode, relations){
	function rec(n){
		var node;
		var child, i, e, condition, tmp;

		switch(n.type){
			case 'relation':
				if(typeof(relations[n.name]) == 'undefined'){
					e = new Error(i18n.t('db.messages.translate.error-relation-not-found', {name: n.name}));
					e.line = n.codeInfo.line;
					e.column = n.codeInfo.column;
					e.codeInfo = n.codeInfo;
					throw e;
				}
				node = relations[n.name].copy();
				break;

			case 'table':
				var schema = new Schema();

				var col;
				for(i = 0; i < n.columns.length; i++){
					col = n.columns[i];
					schema.addColumn(col.name, col.relAlias, col.type);
				}

				var rel = new Relation(n.name);
				rel.setSchema(schema, true);
				rel.addRows(n.rows);
				rel.setMetaData('isInlineRelation', true);
				rel.setMetaData('inlineRelationDefinition', n.codeInfo.text);
				//TODO: inlineRelationDefinition should be replaced; there should be a generic way to get the definition of a node
				node = rel;
				break;

			case 'selection':
				child = rec(n.child);
				condition = rec(n.arg);
				node = new Selection(child, condition);
				break;

			case 'valueExpr':
				if(n.datatype == 'null' && n.func === 'columnValue'){
					node = new ValueExpr.ValueExprColumnValue(n.args[0], n.args[1]);
				}
				else{
					switch(n.datatype){
						case 'string':
						case 'number':
						case 'boolean':
						case 'date':
						case 'null': // all with unknown type
							tmp = [];
							for(i = 0; i < n.args.length; i++){
								if(n.func === 'constant')
									tmp.push(n.args[i]);
								else
									tmp.push( rec(n.args[i]) );
							}

							node = new ValueExpr.ValueExprGeneric(n.datatype, n.func, tmp);
							break;
						default:
							throw 'not implemented yet';
					}
				}
				break;

			case 'projection':
				child = rec(n.child);
				var projections = [];
				for(i = 0; i < n.arg.length; i++){

					e = n.arg[i];

					if(e.type === 'columnName'){
						projections.push(new Column(e.name, e.relAlias));
					}
					else if(e.type === 'namedColumnExpr'){
						// namedColumnExpr
						projections.push({
							name: e.name,
							relAlias: e.relAlias,
							child: rec(e.child)
						});
					}
					else{
						throw 'should not happen';
					}
				}

				node = new Projection(child, projections);
				break;

			case 'orderBy':
				child = rec(n.child);
				var orderCols = [];
				var orderAsc = [];

				for(i = 0; i < n.arg.length; i++){
					e = n.arg[i];

					orderAsc.push(e.asc);
					orderCols.push(new Column(e.col.name, e.col.relAlias));
				}

				node = new OrderBy(child, orderCols, orderAsc);
				break;

			case 'groupBy':
				child = rec(n.child);
				var aggregateFunctions = n.aggregate;
				var groupByCols = n.group;

				node = new GroupBy(child, groupByCols, aggregateFunctions);
				break;

			case 'union':
				node = new Union(rec(n.child), rec(n.child2));
				break;

			case 'intersect':
				node = new Intersect(rec(n.child), rec(n.child2));
				break;

			case 'division':
				node = new Division(rec(n.child), rec(n.child2));
				break;

			case 'difference':
				node = new Difference(rec(n.child), rec(n.child2));
				break;

			case 'renameColumns':
				var ren = new RenameColumns(rec(n.child));

				for(i = 0; i < n.arg.length; i++){
					e = n.arg[i];

					ren.addRenaming(e.dst, e.src.name, e.src.relAlias);
				}

				node = ren;
				break;

			case 'renameRelation':
				node = new RenameRelation(rec(n.child), n.newRelAlias);
				break;

			case 'thetaJoin':
				condition = rec(n.arg);
				node = new InnerJoin(rec(n.child), rec(n.child2), condition);
				break;

			case 'crossJoin':
				node = new CrossJoin(rec(n.child), rec(n.child2));
				break;

			case 'naturalJoin':
				node = new InnerJoin(rec(n.child), rec(n.child2), null);
				break;

			case 'leftSemiJoin':
				node = new SemiJoin(rec(n.child), rec(n.child2), true);
				break;

			case 'rightSemiJoin':
				node = new SemiJoin(rec(n.child), rec(n.child2), false);
				break;

			case 'antiJoin':
				node = new AntiJoin(rec(n.child), rec(n.child2));
				break;

			case 'leftOuterJoin':
				condition = (n.arg === null ? null : rec(n.arg));
				node = new LeftOuterJoin(rec(n.child), rec(n.child2), condition);
				break;

			case 'rightOuterJoin':
				condition = (n.arg === null ? null : rec(n.arg));
				node = new RightOuterJoin(rec(n.child), rec(n.child2), condition);
				break;

			case 'fullOuterJoin':
				condition = (n.arg === null ? null : rec(n.arg));
				node = new FullOuterJoin(rec(n.child), rec(n.child2), condition);
				break;

			default:
				e = new Error('type '+n.type+' not implemented');
				if(n.codeInfo){
					e.line = n.codeInfo.line;
					e.column = n.codeInfo.column;
					e.codeInfo = n.codeInfo;
				}
				throw e;
		}



		node.setCodeInfo(
			n.codeInfo.line,
			n.codeInfo.column,
			n.codeInfo.offset,
			n.codeInfo.text
		);

		if(typeof(n.metaData) != 'undefined'){
			for(var key in n.metaData){
				if(!n.metaData.hasOwnProperty(key))
					continue;

				node.setMetaData(key, n.metaData[key]);
			}
		}

		if(n.wrappedInBrackets === true){
			node.setWrappedInBrackets(true);
		}

		return node;
	}

	return rec(astNode);
}