import { ExecutionError } from './ExecutionError'
import { RANode } from './RANode'
import { Table } from './Table'
import * as ValueExpr from './ValueExpr'

export class Selection extends RANode {
	constructor(child, condition) {
		super('&sigma;');
		this._condition = condition;

		this.setChild(child);
		if (condition instanceof ValueExpr.ValueExpr == false)
			throw new Error('no condition given');

		this._schema = null;
	}

	getSchema() {
		return this._schema;
	}

	getResult(session) {
		session = this._returnOrCreateSession(session);

		var res = new Table();
		var org = this.getChild().getResult(session);
		res.setSchema(org.getSchema());

		// copy
		var condition = this._condition;
		var numRows = org.getNumRows();
		for (var i = 0; i < numRows; i++) {
			var row = org.getRow(i);

			if (condition.evaluate(row, null, i, session) === true)
				res.addRow(row);
		}

		this.setResultNumRows(res.getNumRows());
		return res;
	}

	check() {
		this._child.check();

		// schema of union is the left schema
		this._schema = this._child.getSchema();

		this._condition.check(this._schema);
		if (this._condition.getDataType() !== 'boolean') {
			this.throwExecutionError(i18n.t('db.messages.exec.error-condition-must-be-boolean'));
		}
	}

	getArgumentHtml() {
		return this._condition.getFormulaHtml();
	}
}