import { ExecutionError } from './ExecutionError'
import { RANode } from './RANode'
import { Difference } from './Difference'
import { Projection } from './Projection'
import { CrossJoin } from './CrossJoin'

/**
 * relational algebra division operator
 *
 * @constructor
 * @extends RANode
 * @param   {RANode} child  the left child expression
 * @param   {RANode} child2 the right child expression
 * @returns {Division}
 */
export class Division extends RANode {
	constructor(child, child2) {
		super('÷');

		this.setChild(child);
		this.setChild2(child2);

		// is set by check
		this._delegate = null;
	}

	setChild2(child2) {
		this._child2 = child2;
	}

	getSchema() {
		return this._delegate.getSchema();
	}

	getResult(session) {
		session = this._returnOrCreateSession(session);

		var res = this._delegate.getResult(session);

		res.eliminateDuplicateRows();
		this.setResultNumRows(res.getNumRows());
		return res;
	}

	check() {
		this._child.check();
		this._child2.check();

		// schema r' is (sch(left) \ sch(right))
		var schemaA = this._child.getSchema();
		var schemaB = this._child2.getSchema();
		var numColsA = schemaA.getSize();
		var numColsB = schemaB.getSize();

		var schema = schemaA.copy();
		for (var i = 0; i < numColsB; i++) {
			var index = schema.getColumnIndex(schemaB.getColumn(i).getName(), null, false);
			if (index > -1)
				schema.removeColumn(index);
		}

		if (schema.getSize() === numColsA) { // size has not changed => schemaB not part of schemaA
			this.throwExecutionError(i18n.t('db.messages.exec.error-schema-a-not-part-of-schema-b', {schemaA: schemaB, schemaB: schemaA}));
		}


		// (R % S) := (pi r'(R)) - ( ( (pi r'(R) x (S) )) - (R) )
		this._delegate = new Difference(
			new Projection(this._child, schema.getColumns()).setCodeInfoObject(this._codeInfo),
			new Projection(
				new Difference(
					new CrossJoin(
						new Projection(this._child, schema.getColumns()).setCodeInfoObject(this._codeInfo),
						this._child2
					).setCodeInfoObject(this._codeInfo),
					this._child
				).setCodeInfoObject(this._codeInfo),
				schema.getColumns()
			).setCodeInfoObject(this._codeInfo)
		);
		this._delegate.check();
	}
}